import './Aside.css'
import Iframe from 'react-iframe'
import { useTranslation } from 'react-i18next'
import Img from '../../Assets/Img/background.png';
import Img1 from '../../Assets/Img/n.png';

const Aside = () => {

    const {t} = useTranslation()

    return(
        <div id='location' className="aside">
            {/* <div className="aside-img">
                <img src={n} alt="" className="aside-pic" />
            </div> */}
            <img src={Img} className="back-img1" alt="" />
            <img src={Img1} className="back-img2" alt="" />
            <div className="container">
                <h2 className="aside-name">{t("loc_name")}</h2>

                <div className="aside-map">
                   <Iframe 
                   className='aside-iframe'
                   src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3607.8355473277165!2d55.30310131501109!3d25.276116983859577!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x76c885a0736eae99!2zMjXCsDE2JzM0LjAiTiA1NcKwMTgnMTkuMCJF!5e0!3m2!1sru!2s!4v1670004912104!5m2!1sru!2s"  style="border:0;" allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade" />
                </div>

                <p className="aside-names">{t("locp1")}</p>
                <p className="aside-text">{t("locp2")}</p>
            </div>
        </div>
    )
}
export default Aside