import './Footer.css'
import React from 'react';
import Fade from 'react-reveal/Fade'
import Bounce from 'react-reveal/Bounce';
import { useState } from 'react'
import person from '../../Assets/Img/person.png'
import tels from '../../Assets/Img/tels.png'
import inst from '../../Assets/Img/inst.png'
import tme from '../../Assets/Img/tme.png'

import 'react-phone-number-input/style.css'
import PhoneInput from 'react-phone-number-input'
import { useTranslation } from 'react-i18next';


const Footer = ({ changeModal }) => {

    const [value, setValue] = useState()

    const [greatModal, setGreatModal] = useState(false);

    function openGreatModal() {
        setGreatModal(!greatModal)
    }

    const { t } = useTranslation()

    const formBtn = (e) => {

        e.preventDefault();
        if (e.target[0].value.length > 0
            && e.target[1].value.length > 0 && e.target[2].value.length > 0
        ) {

            let botMessege = `
                     Assalomu allaykum, Sizga Yangi Xabar!😊%0A
                     Ismi 👤: ${e.target[0].value}%0A
                     Raqam ☎: ${e.target[1].value}%0A 
                     Xabar ✏️ ${e.target[2].value}%0A              
                `;

            let url = `https://api.telegram.org/bot5946913626:AAGJprAUXFjE51pw1lmj-rsXw1EEpnpF3Y4/sendMessage?chat_id=-1001855259846&text=${botMessege}`;

            async function fetchAsync(url) {
                let response = await fetch(url);
                let data = await response.json();

                return data;

            }
            fetchAsync(url);

            if (document.querySelector("#name").matches(".input-error")) {
                document.querySelector("#name").classList.remove("input-error")

                document.querySelector("#errorText").classList.remove("error-text1")
            }
            if (document.querySelector("#tel").matches(".tel-error")) {
                document.querySelector("#tel").classList.remove("tel-error")
                document.querySelector("#errorTel").classList.remove("error-tel1")
            }

            if (document.querySelector("#textarea").matches(".error-info")) {
                document.querySelector("#textarea").classList.remove("error-info")
                document.querySelector("#errorInfo").classList.remove("error-info1")
            }


            e.target[0].value = ""


            e.target[1].value = ""

            e.target[2].value = ""

            openGreatModal()
            changeModal()

        }
        else {
            if (e.target[0].value.length < 1) {

                document.querySelector("#name").classList.add("input-error")
                document.querySelector("#errorText").classList.add("error-text1")
            }
            if (e.target[1].value.length < 1) {
                document.querySelector("#tel").classList.add("tel-error")
                document.querySelector("#errorTel").classList.add("error-tel1")
            }
            if (e.target[2].value.length < 1) {
                document.querySelector("#textarea").classList.add("error-info")
                document.querySelector("#errorInfo").classList.add("error-info1")
            }
        }

    }



    return (
        <div id='contact' className="footer">
            <div className="texture5"></div>
            <div className="texture6"></div>
            <div className="container">
                <div className="footer-page" style={{ position: "sticky", zIndex: 2 }}>
                    {/* <Fade left big> */}
                    <div className="footer-left">
                        <h2 className="footer-name">{t("formn1")}</h2>
                        <p className="footer-text">{t("formn2")}</p>
                    </div>
                    {/* </Fade> */}

                    {/* <Fade right big> */}
                    <div className="footer-right">
                        <form id="myForm" onSubmit={formBtn} className="footer-form">
                            <div className="footer-labels">
                                <label className='footer-label'>
                                    <h3 className="footer-names">{t("n1")}</h3>
                                    <div className="footer-inputs">
                                        <input id="name" required type="text" placeholder={`${t("name")}`} className="footer-input" />
                                        {/* <img src={person} alt="" className="footer-img" /> */}
                                    </div>
                                </label>
                                <label className='footer-label'>
                                    <h3 className="footer-names">{t("n2")}</h3>
                                    {/* <PhoneInput className='footer-phone'
                                international  
                                id="tel"
                                countryCallingCodeEditable={false}
                                defaultCountry="UZ"
                                // value={value}
                                onChange={setValue}
                                /> */}

                                    <input id="tel" required type="tel" placeholder={`${t("phone")}`} className="footer-phone" />
                                </label>
                            </div>
                            <h3 className="footer-surname">{t("n3")}</h3>
                            <textarea id="textarea" required className='footer-texts' placeholder={`${t("sms")}`}></textarea>
                            <button type='submit' id="btnSubmit" className="footer-btn">{t("send")}</button>
                        </form>
                    </div>
                    {/* </Fade> */}
                </div>
                <div className="footer-pic">
                    {/* <Bounce bottom> */}
                    <img src={tels} alt="" style={{ zIndex: 4 }} className="footer-imgs" />
                    {/* </Bounce> */}
                </div>

                <div className="footer-box">
                    <div className="footer-title">
                        <h2 className='footer-surname'>{t("locp1")}</h2>
                        <p className="footer-textss">{t("slogan1")}</p>
                        <div className="footer-icon">
                            <a href="https://www.instagram.com/7milliard.uae/" className="footer__link"><img src={inst} alt="" className="footer-logo" /></a>
                            <a href="https://t.me/megastar_uzb" className="footer__link"><img src={tme} alt="" className="footer-logo" /></a>
                        </div>
                    </div>

                    <ul className="footer-list">
                        <li className="footer-item">
                            <h2 className="footer-surname">{t("fn2")}</h2>
                        </li>
                        <li className="footer-item">
                            <p className="footer-message">{t("f1")}</p>
                        </li>
                        <li className="footer-item">
                            <p className="footer-message">{t("f2")}</p>
                        </li>
                        <li className="footer-item">
                            <p className="footer-message">{t("f3")}</p>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    )
}
export default Footer