import './Box.css'
import React, { useEffect } from 'react';
import oltin from '../../Assets/Img/gold.png'
import sap from '../../Assets/Img/sap.png'
import BackImg from '../../Assets/Img/e.png'
import { useState } from 'react';
import { useTranslation } from 'react-i18next';
// import { Rotate } from 'react-reveal';

const Box = () => {


    const [transformX, setTransformX] = useState(0)

    useEffect(() => {
        window.addEventListener("scroll", () => {
            setTransformX(window.scrollY / 4)
        })
    }, [])

    const [imgRightX, setImgRightX] = useState(0)

    const observer = new IntersectionObserver((entries) => {
        entries.forEach((entry) => {
            window.addEventListener("scroll", () => {
                if (-100 + (window.scrollY - entry.target.offsetTop) / 2 < 0) {
                    setImgRightX(window.scrollY - entry.target.offsetTop)
                }
            })
        })
    })

    useEffect(() => {
        const sectionPage = document.getElementById("about")
        observer.observe(sectionPage)
    }, [])

    const { t } = useTranslation()

    return (
        <div className="About" id='about'>
            <img src={BackImg} alt="" className="back-img" />
            <h1 className="title">{t("about")} <img src={oltin} className="gold" alt="" /></h1>
            <div className="texts">
                <p>{t("about_us1")}</p>
                <p>{t("about_us3")}</p>
                <p>{t("about_us5")}</p>
                <p>{t("about_us7")}</p>
                <p>{t("about_us10")}</p>
                <p><img src={sap} className="sap-img" alt="" /></p>
            </div>
            <div className="texture3"></div>
            <div className="texture4"></div>
        </div>
    )
}
export default Box