import './Section.css'
import naushnik from '../../Assets/Img/naushniks.png'
import phone from '../../Assets/Img/image-phone.png'
import { useState } from 'react';
import React from 'react';
import Spin from 'react-reveal/Spin';
import Jump from 'react-reveal/Jump';
import { useEffect } from 'react';
import Head1 from '../../Assets/Img/head1.png'
import Head2 from '../../Assets/Img/head2.png'
import { useTranslation } from 'react-i18next';



const Section = () => {

    const [leftX, setLeftX] = useState(0)

    window.addEventListener("scroll", () => {
        setLeftX(window.scrollY / 35)
        setRightX(-window.scrollY / 35)
    })

    const [rightX, setRightX] = useState(0)
    const [imgLeftX, setImgLeftX] = useState(0)
    const [headLeftX, setHeadLeftX] = useState(0)

    // window.addEventListener("scroll", () => {
    //     setRightX(window.scrollY/12)
    // })

    const [transformX, setTransformX] = useState(0)
    window.addEventListener("scroll", () => {
        setTransformX(window.scrollY / 3)
    })

    const observer = new IntersectionObserver((entries) => {
        entries.forEach((entry) => {
            window.addEventListener("scroll", () => {
                if (-80 + (window.scrollY - entry.target.offsetTop) / 10 < 0) {
                    setImgLeftX(window.scrollY - entry.target.offsetTop)
                }
                if (-1 + (window.scrollY - entry.target.offsetTop) / 5 < 40) {
                    setHeadLeftX(window.scrollY - entry.target.offsetTop)
                }
            })
        })
    })

    console.log(imgLeftX)

    useEffect(() => {
        const sectionPage = document.getElementById("Section")
        observer.observe(sectionPage)
    }, [])

    const { t } = useTranslation()

    return (
        <>
            <div className="section" id='Section'>
                <div className="container">
                    <div className="section-left">
                        <h2 className="section-name" style={{ marginLeft: window.innerWidth > 600 && window.scrollY <= 1200 ? `${leftX}px` : "0px" }}>{t("p2")}</h2>
                        <h2 className="section-name" style={{ marginLeft: window.innerWidth > 600 && window.scrollY <= 1200 ? `${rightX}px` : "0px" }}></h2>
                        <p className="section-text">{t("p2p")}</p>
                        <div className="btns">
                            <a href="#prem" className="explore">{t("btn1")}</a>
                        </div>
                    </div>

                    <div className="section-right">
                        {/* style={{ left: `${-1 + headLeftX / 5}%` }} */}
                        <img src={Head1} alt="" />
                    </div>
                    <div className="section-right1">
                        {/* style={{ right: `${-1 + headLeftX / 5}%` }} */}
                        <img src={Head2} alt="" />
                    </div>
                </div>
                <div className="texture3"></div>
                <div className="texture4"></div>
            </div>

            <div className="section-box" id='prem'>
                <div className="container con1">
                    <div className="section-lefts">
                        <h2 className="section-names" style={{ marginLeft: window.innerWidth > 600 && window.scrollY <= 1900 ? `${leftX}px` : "0px" }}>{t("p3")}</h2>
                        <h2 className='section-names' style={{ marginLeft: window.innerWidth > 600 && window.scrollY <= 1900 ? `${rightX}px` : "0px" }}></h2>
                        <p className="section-texts">{t("p3p")}</p>
                        <div className="btns section-texts sec-btn">
                            <a href="#sublime" className="explore">{t("btn1")}</a>
                        </div>
                    </div>
                    <div className="section-rights">
                        {/* style={{ left: `${-80 + imgLeftX / 10}%` }} */}
                        {/* <Jump> */}
                        <img src={phone} alt="" className="section-pic" />
                        {/* </Jump> */}
                    </div>
                </div>
                <div className="texture3"></div>
                <div className="texture4"></div>
            </div>
        </>
    )
}
export default Section