import { useState } from 'react';
import { useTranslation } from 'react-i18next';
import './App.css';
import Aside from './Components/Aside/Aside';
import Box from './Components/Box/Box';
import Footer from './Components/Footer/Footer';
import Header from './Components/Header/Header';
import ModalContact from './Components/ModalContact/ModalContact';
import Navbar from './Components/Navbar/Navbar';
import ScrollPercent from './Components/ScrollPercent/ScrollPercent';
import Section from './Components/Section/Section';
import Sublime from './Components/Sublime/Sublime';
import Loader from './Loader/Loader';


function App() {

  // languages

  const { i18n } = useTranslation()

  const [english, setEnglish] = useState(localStorage.getItem('i18nextLng') ? localStorage.getItem('i18nextLng') == "en" ? true : false : false);
  const [russian, setRussian] = useState(localStorage.getItem('i18nextLng') ? localStorage.getItem('i18nextLng') == "ru" ? true : false : false);
  const [uzbek, setUzbek] = useState(localStorage.getItem('i18nextLng') ? localStorage.getItem('i18nextLng') == "uz" ? true : false : false);
  const [langTitle, setLangTitle] = useState(localStorage.getItem('i18nextLng') ? localStorage.getItem('i18nextLng').toUpperCase() : "")

  function changeUzbek(item) {
    setUzbek(item)
    setEnglish(!item)
    setRussian(!item)
    i18n.changeLanguage("uz")
    setLangTitle(localStorage.getItem('i18nextLng').toUpperCase())
  }

  function changeRussian(item) {
    setUzbek(!item)
    setRussian(item)
    setEnglish(!item)
    i18n.changeLanguage("ru")
    setLangTitle(localStorage.getItem('i18nextLng').toUpperCase())
  }

  function changeEnglish(item) {
    setUzbek(!item)
    setEnglish(item)
    setRussian(!item)
    i18n.changeLanguage("en")
    setLangTitle(localStorage.getItem('i18nextLng').toUpperCase())
  }

  // modal

  const [showModal, setShowModal] = useState(false)

  function changeModal() {
    setShowModal(!showModal)
  }

  return (
    <div className="App">
      {/* <Loader /> */}
      <ScrollPercent />
      <Navbar langTitle={langTitle} changeUzbek={changeUzbek} changeRussian={changeRussian} changeEnglish={changeEnglish} uzbek={uzbek} english={english} russian={russian} />
      <Header changeUzbek={changeUzbek} changeRussian={changeRussian} changeEnglish={changeEnglish} uzbek={uzbek} english={english} russian={russian} />
      <Section />
      <Sublime />
      <Box />
      <Aside />
      <Footer changeModal={changeModal} />
      {
        showModal &&
        <ModalContact english={english} russian={russian} changeModal={changeModal} />
      }
    </div>
  );
}

export default App;
