import './Sublime.css'
import clock from '../../Assets/Img/clock.png'
import { useEffect, useState } from 'react'
import React from 'react';
import Flash from 'react-reveal/Flash';
import { useTranslation } from 'react-i18next';

const Sublime = () => {

    const [leftX, setLeftX] = useState(0)

    window.addEventListener("scroll", () => {
        // setLeftX(window.scrollY / 35)
        setRightX(-window.scrollY / 35)
    })

    const [transformX, setTransformX] = useState(0)
    window.addEventListener("scroll", () => {
        setTransformX(-window.scrollY / 5)
    })
    console.log(window.scroll)

    const [rightX, setRightX] = useState(0)
    const [imgRightX, setImgRightX] = useState(0)

    const observer = new IntersectionObserver((entries) => {
        entries.forEach((entry) => {
            window.addEventListener("scroll", () => {
                if ((window.scrollY - entry.target.offsetTop) > -window.innerHeight) {
                    setImgRightX(window.scrollY - entry.target.offsetTop)
                    setLeftX((window.scrollY - entry.target.offsetTop) / 35)
                }
            })
        })
    })

    useEffect(() => {
        const sectionPage = document.getElementById("sublime")
        observer.observe(sectionPage)
    }, [])

    const { t } = useTranslation()

    return (
        <div className="sublime" id='sublime'>
            <div className="container">
                <div className="sublime-left">
                    <h2 className="sublime-name" style={{ marginLeft: window.innerWidth > 520 && `${leftX}px` }}>{t("p4")}</h2>
                    <p className="sublime-text">{t("p4p")}</p>
                    <div className="btns">
                        <a href="#about" className="explore">{t("btn1")}</a>
                    </div>
                </div>
                <div className="sublime-right">
                    {/* <Watch /> */}

                    <img src={clock} alt="" className="sublime-img" />

                    {/* style={{ marginLeft: `${100 - imgRightX / 10}%` }} */}
                </div>
            </div>
            <div className="texture3"></div>
            <div className="texture4"></div>
        </div>
    )
}
export default Sublime