import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import Logo from "../../Assets/Img/logo.png"
import './Navbar.css'
import menuIcon from "../../Assets/Img/menu.png"

const Navbar = ({ english, russian, uzbek, changeUzbek, changeRussian, changeEnglish, langTitle }) => {

    const { t } = useTranslation()

    useEffect(() => {
        let nav = document.getElementById("nav")
        window.addEventListener("scroll", () => {
            nav.classList.toggle("black-nav", window.scrollY > 10)
        })
    }, [])

    //language

    const [lang, setlang] = useState(false);

    function getUzbek() {
        changeUzbek(true)
        setlang(!lang)
    }
    function getRussian() {
        changeRussian(true)
        setlang(!lang)
    }
    function getEnglish() {
        changeEnglish(true)
        setlang(!lang)
    }

    const [show, setshow] = useState(false);

    return (
        <div className='Navbar' id='nav'>
            <nav className="navbar">
                <a href="#" className="navbar-brand">
                    <img src={Logo} alt="" />
                </a>
                <ul className={`navbar-nav ${show && "show"}`}>
                    <li className="nav-item">
                        <a href="#" className="nav-link">
                            {t("h1")}
                        </a>
                    </li>
                    <li className="nav-item">
                        <a href="#sublime" className="nav-link">
                            {t("h2")}
                        </a>
                    </li>
                    <li className="nav-item">
                        <a href="#about" className="nav-link">
                            {t("h3")}
                        </a>
                    </li>
                    <li className="nav-item">
                        <a href="#location" className="nav-link">
                            {t("h4")}
                        </a>
                    </li>
                    <li className="nav-item">
                        <a href="#contact" className="nav-link">
                            {t("h5")}
                        </a>
                    </li>
                    <li className="languages">
                        <div className="lang-title" onClick={() => setlang(!lang)}>{langTitle} <i className="fa fa-angle-down"></i></div>
                        <div className={`lang-menu ${!lang && "d-none"}`}>
                            {/* <div className="lang" onClick={() => getUzbek()}>UZ</div> */}
                            <div className="lang" onClick={() => getRussian()}>RU</div>
                            <div className="lang" onClick={() => getEnglish()}>EN</div>
                        </div>
                    </li>                
                    <li className="nav-item">
                        <a href="tel:+971 54 755 7700" className="nav-links nav-tel">
                            <i className="fa fa-phone"></i>
                            {t("f1")}
                        </a>
                    </li>
                    <div className="close-icon" onClick={() => setshow(false)}>&times;</div>
                </ul>
                <div className="open-icon" onClick={() => setshow(true)}>
                    <img src={menuIcon} alt="" />
                </div>
            </nav>
        </div>
    );
};

export default Navbar;